import { Component } from '@angular/core';

@Component({
  selector: 'app-root',
  templateUrl: './app.component.html',
  styleUrls: ['./app.component.css']
})
export class AppComponent {
  authState = 'false';
  onChanged(increased:any){
    if (increased == 'true') {
      this.authState = 'true';
    }
  }
  onChangedMenu(increased:any) {
    if (increased == 'game') {
      this.authState = 'game';
    }
  }
}


