import { Component, OnInit } from '@angular/core';
import { GameService } from './game.service';
import { forEach } from '@angular/router/src/utils/collection';
import {
  trigger,
  state,
  style,
  animate,
  transition
} from '@angular/animations';
import { Router } from '@angular/router';

@Component({
  selector: 'game-comp',
  templateUrl: './game.html',
  styleUrls: ['./game.css'],
  providers: [ 
    GameService,
  ],
  animations: [
    trigger('userItem', [
      state('inactive, void', style({
          display: 'none',
          opacity: '0',
      })),
      state('user',   style({
          display: 'flex',
          opacity: '1',
      })),
      transition('inactive => user', [ animate('0.8s ease-in-out', style({ opacity: '1' })) ]),
      transition('user => inactive', [ animate('0.8s ease-in-out', style({ opacity: '0' })) ]),
    ]),
    trigger('botItem', [
      state('inactive, void', style({
          display: 'none',
          opacity: '0',
      })),
      state('bot',   style({
          display: 'flex',
          opacity: '1',
      })),
      transition('inactive => user', [ animate('0.8s ease-in-out', style({ opacity: '1' })) ]),
      transition('user => inactive', [ animate('0.8s ease-in-out', style({ opacity: '0' })) ]),
    ]),
    trigger('nullItem', [
      state('inactive, void', style({
          display: 'none',
          opacity: '0',
      })),
      state('null',   style({
          display: 'flex',
          opacity: '1',
      })),
      transition('inactive => user', [ animate('0.8s ease-in-out', style({ opacity: '1' })) ]),
      transition('user => inactive', [ animate('0.8s ease-in-out', style({ opacity: '0' })) ]),
    ]),
  ]
})
export class GameComponent implements OnInit {
  public userState = 'inactive';
  public botState = 'inactive';
  public nullState = 'inactive';
  data:string;
  randDeck;
  rand;
  arrUser = [];
  userAttack = 0;
  arrBot = [];
  botAttack = 0;
  constructor(private gameService: GameService){};
  ngOnInit() {
    this.gameService.getData().subscribe((data) => {
      for (var i = 0; i < 3; i++) {
        this.randDeck = Math.floor(Math.random() * (4 - 0)) + 0;
        if (this.randDeck == 0) {
          this.rand = Math.floor(Math.random() * (4 - 0)) + 0;
          var arrTemp1 = [];
          arrTemp1.push(data.alldeck.deck1[this.rand]);
          arrTemp1.push('deck1');
          this.arrUser.push(arrTemp1);
        } else if (this.randDeck == 1) {
          this.rand = Math.floor(Math.random() * (4 - 0)) + 0;
          var arrTemp1 = [];
          arrTemp1.push(data.alldeck.deck2[this.rand]);
          arrTemp1.push('deck2');
          this.arrUser.push(arrTemp1);
        } else if (this.randDeck == 2) {
          this.rand = Math.floor(Math.random() * (4 - 0)) + 0;
          var arrTemp1 = [];
          arrTemp1.push(data.alldeck.deck3[this.rand]);
          arrTemp1.push('deck3');
          this.arrUser.push(arrTemp1);
        } else if (this.randDeck == 3) {
          this.rand = Math.floor(Math.random() * (4 - 0)) + 0;
          var arrTemp1 = [];
          arrTemp1.push(data.alldeck.deck4[this.rand]);
          arrTemp1.push('deck4');
          this.arrUser.push(arrTemp1);
        }
      }
      for (var i = 0; i < 3; i++) {
        this.randDeck = Math.floor(Math.random() * (4 - 0)) + 0;
        if (this.randDeck == 0) {
          this.rand = Math.floor(Math.random() * (4 - 0)) + 0;
          var arrTemp1 = [];
          arrTemp1.push(data.alldeck.deck1[this.rand]);
          arrTemp1.push('deck1');
          this.arrBot.push(arrTemp1);
        } else if (this.randDeck == 1) {
          this.rand = Math.floor(Math.random() * (4 - 0)) + 0;
          var arrTemp1 = [];
          arrTemp1.push(data.alldeck.deck2[this.rand]);
          arrTemp1.push('deck2');
          this.arrBot.push(arrTemp1);
        } else if (this.randDeck == 2) {
          this.rand = Math.floor(Math.random() * (4 - 0)) + 0;
          var arrTemp1 = [];
          arrTemp1.push(data.alldeck.deck3[this.rand]);
          arrTemp1.push('deck3');
          this.arrBot.push(arrTemp1);
        } else if (this.randDeck == 3) {
          this.rand = Math.floor(Math.random() * (4 - 0)) + 0;
          var arrTemp1 = [];
          arrTemp1.push(data.alldeck.deck4[this.rand]);
          arrTemp1.push('deck4');
          this.arrBot.push(arrTemp1);
        }
      }
    });
  }
  block() {

  }
  attack() {
    for(var i = 0; i <= this.arrUser.length -1; i++) {
      this.userAttack = this.userAttack + this.arrUser[i][0].attack;
    }
    for(var i = 0; i <= this.arrBot.length -1; i++) {
      this.botAttack = this.botAttack + this.arrBot[i][0].attack;
    }
    console.log(this.userAttack);
    console.log(this.botAttack);
    if (this.userAttack > this.botAttack) {
      this.userState = 'user';
    } else if (this.userAttack < this.botAttack) {
      this.botState = 'bot';
    } else {
      this.nullState = 'null';
    }
  }
  next() {

  }
}


