import { 
  Component,
  EventEmitter, 
  Input, 
  Output 
} from '@angular/core';
import { Router } from '@angular/router';

@Component({
  selector: 'menu-comp',
  templateUrl: './menu.html',
  styleUrls: ['./menu.css']
})
export class MenuComponent {
  constructor(private router: Router){}
  goToGame() {
    this.router.navigate(['game']);
 }
}


