import { 
    Component,
    EventEmitter, 
    Input, 
    Output 
} from '@angular/core';
import {
    trigger,
    state,
    style,
    animate,
    transition
} from '@angular/animations';

import { AuthService } from './auth.service';
import { RegService } from './reg.service';
import { User } from './user';
import { Router } from '@angular/router';

@Component({
    selector: 'auth-comp',
    templateUrl: './auth.html',
    styleUrls: ['./auth.css'],
    providers: [
        AuthService,
        RegService
    ],
    animations: [
        trigger('selectItemActive', [
            state('active, void', style({
                display: 'block',
                opacity: '1',
            })),
            state('inactive',   style({
                display: 'none',
                opacity: '0',
            })),
            transition('inactive => active', [ animate('0.2s 200ms ease-in-out', style({ opacity: '1' })) ]),
            transition('active => inactive', [ animate('0.2s ease-in-out', style({ opacity: '0' })) ]),
        ]),
        trigger('selectItem', [
            state('active, void', style({
                display: 'none',
                opacity: '0',
            })),
            state('inaction',   style({
                display: 'block',
                opacity: '1',
            })),
            transition('inactive => active', [ animate('0.2s ease-in-out', style({ opacity: '0' })) ]),
            transition('active => inactive', [ animate('0.2s 200ms ease-in-out', style({ opacity: '1' })) ]),
        ]),
    ]
})
export class AuthComponent {
    mail:string;
    pass:string;
    public selectState = 'inactive';
    formSelectorBtnClick() {
        this.selectState = this.selectState === 'active' ? 'inactive' : 'active';
    }

    constructor(private router: Router){}
    goToMenu() {
        if (this.mail == 'player1' && this.pass == 'qwerty') {
            this.router.navigate(['menu']);
        } else if (this.mail == 'player2' && this.pass == 'qwerty') {
            this.router.navigate(['menu']);
        } else {
            console.log('auth err');
        }
   }
}


